export default {
	data() {
		return {
			showLoading:false,
			image:"",
			text:"",
		};
	},
	methods:{
		show:function(config){
			if(config['image'] != undefined )  this.image = config['image']
			if(config['text'] != undefined )  this.text = config['text']
			this.showLoading = true;
		},
		hide:function(){
			this.showLoading = false;
		},
		moveHandleStop:function(){
			
		}
	}
}
